.bejelentkezes,
.nyitvatartas,
.cim {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
    margin: 1rem;

    h2 {
        margin-bottom: 1rem;
    }
}

.bejelentkezes {
    a:not(a:last-child) {
        margin-bottom: 0.5rem;
    }

    a {
        display: flex;
        align-items: center;
        color: black;

        .icon {
            margin-right: 1rem;
        }
    }

    a {
        border-bottom: 2px solid transparent;
        transition: all 150ms ease-in-out;
        color: $primary-font-color;

        &:hover {
            border-color: $secondary-font-color;
        }
    }

}

.nyitvatartas {
    ul {
        display: flex;
        flex-direction: column;
        align-items: center;
        list-style: none;
        width: 100%;

        li:not(li:last-child) {
            margin-bottom: 0.5rem;
        }

        li {
            display: flex;
            justify-content: center;
            min-width: 250px;

            span:first-child {
                margin-right: 1rem;
            }
        }
    }
}

.cim {
    a {
        display: flex;
        flex-direction: column;
        align-items: center;
        border-bottom: 2px solid transparent;
        transition: all 150ms ease-in-out;
        color: $primary-font-color;

        &:hover {
            border-color: $secondary-font-color;
        }

        p {
            font-size: 1rem;
        }

        p:not(p:last-child) {
            margin-bottom: 0.5rem;
        }

    }
}