* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
}

h1 {
  font-weight: 700;
  color: rgb(80, 80, 80);
  font-size: 3rem;
}

h2 {
  font-weight: 400;
  color: rgb(80, 80, 80);
}

h3 {
  font-weight: 400;
  color: rgb(80, 80, 80);
}

p {
  font-size: 1.15rem;
  color: rgb(100, 100, 100);
}

.link-set .icon {
  transition: all 150ms ease-in-out;
}
.link-set .icon:hover {
  scale: 1.25;
}

button {
  border: 1px solid black;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1rem;
  font-weight: 400;
  cursor: pointer;
  transition: all 150ms ease-in-out;
  background-color: rgb(240, 240, 240);
  color: black;
}
button:hover {
  background-color: black;
  color: white;
}

header {
  display: flex;
  position: fixed;
  z-index: 1000;
  width: 100%;
  align-items: center;
  flex-direction: column;
}

.container {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.container .triangle {
  display: block;
  width: 100%;
}
.container .content-box {
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 100px;
}

.head-img-container {
  display: flex;
  justify-content: center;
  height: 600px;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgb(246, 251, 253);
}
.head-img-container .head-content-box {
  display: flex;
  align-items: center;
  height: 100%;
  width: 70%;
}

.bejelentkezes,
.nyitvatartas,
.cim {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  margin: 1rem;
}
.bejelentkezes h2,
.nyitvatartas h2,
.cim h2 {
  margin-bottom: 1rem;
}

.bejelentkezes a:not(a:last-child) {
  margin-bottom: 0.5rem;
}
.bejelentkezes a {
  display: flex;
  align-items: center;
  color: black;
}
.bejelentkezes a .icon {
  margin-right: 1rem;
}
.bejelentkezes a {
  border-bottom: 2px solid transparent;
  transition: all 150ms ease-in-out;
  color: rgb(80, 80, 80);
}
.bejelentkezes a:hover {
  border-color: rgb(100, 100, 100);
}

.nyitvatartas ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  width: 100%;
}
.nyitvatartas ul li:not(li:last-child) {
  margin-bottom: 0.5rem;
}
.nyitvatartas ul li {
  display: flex;
  justify-content: center;
  min-width: 250px;
}
.nyitvatartas ul li span:first-child {
  margin-right: 1rem;
}

.cim a {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 2px solid transparent;
  transition: all 150ms ease-in-out;
  color: rgb(80, 80, 80);
}
.cim a:hover {
  border-color: rgb(100, 100, 100);
}
.cim a p {
  font-size: 1rem;
}
.cim a p:not(p:last-child) {
  margin-bottom: 0.5rem;
}/*# sourceMappingURL=App.css.map */