@import './fonts-and-colors.scss';

// Reset
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Ubuntu', sans-serif;
    font-weight: 300;
}

h1 {
    font-weight: 700;
    color: $primary-font-color;
    font-size: 3rem;
}

h2 {
    font-weight: 400;
    color: $primary-font-color;
}

h3 {
    font-weight: 400;
    color: $primary-font-color;
}

p {
    font-size: 1.15rem;
    color: $secondary-font-color;
}

.link-set {
    .icon {
        transition: all 150ms ease-in-out;

        &:hover {
            scale: 1.25;
        }
    }
}

button {
    border: 1px solid black;
    padding: 0.5rem 1rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 1rem;
    font-weight: 400;
    cursor: pointer;
    transition: all 150ms ease-in-out;
    background-color: $button-bg-color;
    color: $button-font-color;

    &:hover {
        background-color: $button-hover-bg-color;
        color: $button-hover-font-color;
    }
}

header {
    display: flex;
    position: fixed;
    z-index: 1000;
    width: 100%;
    align-items: center;
    flex-direction: column;
}

.container {
    //background-color: rgba(221, 23, 23, 0.5);
    display: flex;
    flex-direction: column;
    width: 100%;
    //margin-top: -100px;

    .triangle {
        display: block;
        width: 100%;
    }

    .content-box {
        display: flex;
        justify-content: center;
        width: 100%;
        min-height: 100px;
    }
}

.head-img-container {
    display: flex;
    justify-content: center;
    height: 600px;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: rgb(246, 251, 253);

    .head-content-box {
        display: flex;
        align-items: center;
        height: 100%;
        width: 70%;
        //background-color: rgba(221, 23, 23, 0.5);
    }
}

